  display: flex;
  padding: 28.5px 30px 26.5px;
  text-decoration: none;
  color: #000000;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
}
.user-list__link:hover,
.user-list__link:focus,
.user-list__link:focus-visible {
  outline: none;
  color: rgba(0, 0, 0, 0.6);
  fill: #61d1bb;
}
.user-list__link:hover .user-list__icon,
.user-list__link:focus .user-list__icon,
.user-list__link:focus-visible .user-list__icon {
  color: rgba(0, 0, 0, 0.6);
  fill: #61d1bb;
}
.user-list__link:active {
  color: rgba(0, 0, 0, 0.3);
  fill: rgba(97, 209, 187, 0.3);
}
.user-list__link:active .user-list__icon {
  color: rgba(0, 0, 0, 0.3);
  fill: rgba(97, 209, 187, 0.3);
}
.user-list__icon {
  min-width: 18px;
  min-height: 18px;
  display: block;
  fill: #00000033;
  margin-right: 25px;
}
.user-list__icon--search {
  margin-left: 2px;
}
.user-list__status {
  display: inline-block;
  word-wrap: break-word;
}
@media (max-width: 767px) {
  .user-list__link {
    padding: 27.5px 30px 26.5px;
  }
  .user-list__icon--search {
    margin: 3px 26px 0 2px;
  }
  .user-list__icon--cart {
    margin: 3px 25px 0 0;
  }
  .user-list__status--cart {
    margin-top: 1px;
  }
}
@media (min-width: 768px) {
  .user-list {
    display: grid;
    grid-auto-flow: row dense;
    grid-template-columns: minmax(min-content, max-content) minmax(min-content, max-content);
    align-content: flex-start;
  }
  .user-list__item {
    box-shadow: none;
    min-height: 89px;
    max-width: 300px;
    border-left: solid 1px #0000001a;
  }
  .user-list__link {
    box-sizing: border-box;
    display: flex;
    height: 100%;
    align-items: center;
    padding: 25px 31px 25px 35px;
  }
  .user-list__icon--search {
    display: block;
    margin: 0;
    margin-left: -2px;
  }
  .user-list__icon--cart {
    display: block;
    margin: -2px 31px 0 -4px;
  }
  .user-list__status--search {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    white-space: nowrap;
    border: 0;
    clip: rect(0 0 0 0);
    clip-path: inset(100%);
  }
}
@media (min-width: 1150px) {
  .user-list__link {
    padding: 25px 33px 25px 35px;
  }
  .user-list__icon--search {
    margin-left: -4px;
  }
}
.youtube-button {
  display: block;
  width: 100.6px;
  height: 76.8px;
  background-image: url("../img/icons/icon-play-video.svg");
  background-repeat: no-repeat;
  background-position: center;
}
